export default {

	data() {
		return {
			userInfo:{},
			isFocus:false
		}
	},
	created() {
		var userInfo = uni.getStorageSync("globalUser")
		if(userInfo!==null&&userInfo!==""&&userInfo!==undefined){
			this.userInfo=userInfo
		}
	},
	methods: {
		searchMe(e){
			var value= e.detail.value
			this.$emit('search',value)
		},
	},
	computed:{
				face: function () {
					var userInfo = uni.getStorageSync("globalUser")
					if(userInfo!==null&&userInfo!==""&&userInfo!==undefined){
						return true
					}
					return false
		    }
	},
	
	
}
